<template>
    <div class="page-match">
        <div v-for="item in gameList" :key="item.top_option.option_id" class="list" @click="goGameDetail(item)">
            <component-match-info :gameInfo="item">
                <template #data>
                    <div class="middle-vs">
                        <div class="data">{{item.top_option.bet_nums[0].score}}</div>
                        <div class="data">{{item.round}}</div>
                        <div class="data">{{item.top_option.bet_nums[1].score}}</div>
                    </div>
                </template>
            </component-match-info>
        </div>
        <!-- <div class="list" @click="goGameDetail">
            <component-match-info>
                <template #data>
                    <div class="middle-vs">
                        <div class="data">1</div>
                        <div class="data">BO3</div>
                        <div class="data">3</div>
                    </div>
                </template>
            </component-match-info>
        </div> -->
    </div>
</template>

<script>
import ComponentMatchInfo from './../../components/ComponentMatchInfo';
import { sendWSPush  } from './../../websocket/websocket.js';
export default {
    components: {
        ComponentMatchInfo
    },
    name: 'match',
    data() {
        return {
            gameList: []
        }
    },
    mounted() {
        // 发送请求websocket
        sendWSPush({
            cmd: 'match_list',
            group: 'gameinfo',
            seq: 0,
            data: {
                utype: 0,
                game_type_ids: [4, 5, 7, 10],
                channel_id: 1,
                game_status: 1,
                data_type: 2
            }
        });
        window.addEventListener("onmessageWS", (e) => { 
            if (!e.detail && !e.detail.data) return;
            let result = JSON.parse(e.detail.data);
            let { code, cmd, data } = result;
            if (code == 200 && cmd == "match_list") {
                this.gameList = data;
            }
        });
    },
    methods: {
        goGameDetail(data) {
            this.$store.commit("WEBSOCKET_ITEM", data);
            this.$router.push({ path: '/home/matchDetail' });
        }
    }
}
</script>
<style lang="less" scoped>
    @import url('./../../assets/style.less');
    .page-match {
        .middle-vs {
            margin: 50px 0 0 0;
            display: flex;
            font-weight: bold;
            font-size: 25px;
            .data {
                flex: 1;
                &:first-child {
                    text-align: left;
                }
                &:last-child {
                    text-align: right;
                }
            }
        }
        .list {
            margin-bottom: 3px;
            cursor: pointer;
            &::v-deep .game {
                border: 1px solid @color-line;
                .top {
                    background: transparent !important;
                }
                .game-data {
                    margin: -60px 0 0 0;
                    .top-vs {
                        visibility: hidden;
                    }
                }
            }
        }


        .list {
            position: relative;
            width: 966px;
            height: 189px;
        }
        .list:before,.list:after{
            content: "";
            display: block;
            width: 0;
            height: 0;
            border: 2px solid transparent;
            box-sizing: border-box;
            position: absolute;
        }
        .list:before {
            top:0;
            left:0;
            // transition: border-color 0s ease-in 0.8s, width 0.2s ease-in 0.6s, height 0.2s ease-in 0.4s;
        }
        .list:after{
            right: 0;
            bottom: 0;
            // transition: border-color 0s ease-in 0.4s, width 0.2s ease-in 0.2s, height 0.2s ease-in;
        }
        
        .list:hover:before{
            width: 100%;
            height: 100%;
            transition: width 0.2s ease-in, height 0.2s ease-in 0.2s;
            border-top-color: #28ffd4;
            border-right-color: #28ffd4;
        }
        .list:hover:after{
            width:100%;
            height:100%;
            transition:  width 0.2s ease-in, height 0.2s ease-in 0.2s;
            border-bottom-color: #28ffd4;
            border-left-color:#28ffd4;
        }


    }
</style>
