<template>
    <div class="page-home">
        <component-carousel></component-carousel>
        <div class="platformData">
            <div class="left">
                <div class="status">
                    <ul>
                        <li>
                            <el-radio v-model="gameStatus" @change="toggleGameStatus" label="1">今日<span>37</span></el-radio>
                        </li>
                        <li>
                            <el-radio v-model="gameStatus" @change="toggleGameStatus" label="2">未开始<span>37</span></el-radio>
                        </li>
                        <li>
                            <el-radio v-model="gameStatus" @change="toggleGameStatus" label="3">已结束<span>37</span></el-radio>
                        </li>
                    </ul>
                </div>
                <div class="currency">
                    <div class="price"><img src="./../assets/wallet/coin_flat.png" />平台币 100000</div>
                    <div class="price"><img src="./../assets/wallet/coin_quizzes.png" />竞猜币 100000</div>
                    <div class="refresh"><img src="./../assets/wallet/refresh1.png" /></div>
                </div>
            </div>
            <div class="right">
                <div class="record">下单记录</div>
                <div class="rule">玩法规则</div>
            </div>
        </div>

        <div class="content">
            <div class="left">
                <div class="game-type">
                    <div v-for="item in gameList" :key="item.id" class="block">
                        <el-radio v-model="gameType" @change="toggleGameType" :label="item.id">
                            <div class="image">
                                <!-- src="./../assets/home/logo/homelogo.png"  -->
                                <!-- <img :src="item.game_logo" /> -->
                                <el-image class="image" :src="item.game_logo">
                                    <div slot="placeholder" class="image-placeholder">
                                        <i class="el-icon-loading"></i>
                                    </div>
                                    <div slot="error" class="image-error">
                                        <i class="el-icon-picture-outline"></i>
                                    </div>
                                </el-image>
                            </div>
                            <div class="text">{{item.name}}<span>37</span></div>
                        </el-radio>
                    </div>
                    <!-- <div class="block">
                        <el-radio v-model="gameType" @change="toggleGameType" label="2">
                            <div class="image"><img src="./../assets/home/logo/lol.png" /></div>
                            <div class="text">英雄联盟<span>37</span></div>
                        </el-radio>
                    </div>
                    <div class="block">
                        <el-radio v-model="gameType" @change="toggleGameType" label="3">
                            <div class="image"><img src="./../assets/home/logo/kog.png" /></div>
                            <div class="text">英雄联盟<span>37</span></div>
                        </el-radio>
                    </div>
                    <div class="block">
                        <el-radio v-model="gameType" @change="toggleGameType" label="4">
                            <div class="image"><img src="./../assets/home/logo/DOTA2.png" /></div>
                            <div class="text">英雄联盟<span>37</span></div>
                        </el-radio>
                    </div>
                    <div class="block">
                        <el-radio v-model="gameType" @change="toggleGameType" label="5">
                            <div class="image"><img src="./../assets/home/logo/csgo.png" /></div>
                            <div class="text">英雄联盟<span>37</span></div>
                        </el-radio>
                    </div> -->
                </div>
                <div class="game-list">
                    <keep-alive include="match">
                        <router-view />
                    </keep-alive>
                </div>
            </div>
            <div class="right">
                <el-tabs v-model="tabsActive" @tab-click="handleClick">
                    <el-tab-pane label="我的下单" name="first">
                        <div class="myOrder">
                            <div v-if="orderList.length > 0">
                                <div v-for="item in orderList" :key="item.bet_id">
                                    <div class="orderHead">
                                        <div class="match" v-if="item.auto_data_type == 1">比赛盘</div>
                                        <div class="match color" v-else>彩池盘</div>
                                        <div class="title">
                                            <div class="name">{{item.auto_option}} <span>{{item.tag}}</span></div>
                                            <div class="num">{{item.auto_name}}</div>
                                        </div>
                                        <div class="image"><img @click="deleteOrder(item)" src="./../assets/common/close_icon.png" /></div>
                                    </div>
                                    <div class="orderContent">
                                        <ul>
                                            <li>{{item.auto_match_name}}</li>
                                            <li><span v-if="item.auto_data_type == 1">@{{item.odds}}</span></li>
                                            <li>{{item.auto_team[0].team_name}} - VS - {{item.auto_team[1].team_name}}</li>
                                            <li>
                                                <div>
                                                    <span v-if="item.auto_data_type == 1">预计返还 200</span>
                                                </div>
                                                <div class="price"><input type="text" value="5" /></div>
                                            </li>

                                            <li></li>
                                        </ul>
                                    </div>
                                </div>

                                <!-- <div>
                                    <div class="orderHead">
                                        <div class="match">比赛盘</div>
                                        <div class="title">
                                            <div class="name">获胜者 <span>88rising</span></div>
                                            <div class="num">第三局</div>
                                        </div>
                                        <div class="image"><img src="./../assets/common/close_icon.png" /></div>
                                    </div>
                                    <div class="orderContent">
                                        <ul>
                                            <li>这是一场倒拉比赛</li>
                                            <li>下单数量：200</li>
                                        </ul>
                                    </div>
                                </div> -->
                                <div class="betting">
                                    <div class="text">已选 <span>{{orderList.length}}</span> 注</div>
                                    <div class="button">
                                        已投注 500
                                    </div>
                                </div>
                            </div>
                            
                            <div v-if="orderList.length <= 0">
                                {{orderList}}
                                <p>您的投注单是空的</p>
                                <p>请添加投注以开始投注</p>
                            </div>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="订单列表" name="second">
                        <div class="orderList">
                            <div v-if="orderList.length > 0">
                                <div class="orderHead">
                                    <div class="match">比赛盘</div>
                                    <div class="name">获胜者 <span>88rising</span></div>
                                    <div class="num">第三局</div>
                                    <!-- <div class="image"><img src="./../assets/common/close_icon.png" /></div> -->
                                </div>
                                <div class="orderContent">
                                    <ul v-for="order in orderList" :key="order.bet_id">
                                        <li>这是一场倒拉比赛</li>
                                        <li>下单数量：200</li>
                                        <li @click="deleteOrder(order)"><img src="./../assets/common/close_icon.png" /></li>
                                    </ul>
                                </div>
                                <div class="betting">
                                    <div class="text">已选 <span>5</span> 注</div>
                                    <div class="button">
                                        已投注 500
                                    </div>
                                </div>
                            </div>
                            <div v-if="orderList.length <= 0">
                                暂无订单，快去玩吧
                            </div>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </div>

        <el-button @click="load()">load</el-button>
        homeData:
        {{homeData}}
        <br />
        {{commonData}}
        <br />
        <p><router-link :to="{path : '/home/home2'}">home2</router-link></p>
        <p><router-link :to="{path : '/home/home2/Ahome3'}">home3</router-link></p>
        <p><router-link :to="{path : '/home/home2/AhomeHaHa3'}">homeHaHa3</router-link></p>
        <p><router-link :to="{path : '/home/AhomeHaha'}">homeHaha</router-link></p>
        <p><router-link :to="{path : '/home/AhomeHaha2'}">homeHaha2</router-link></p>
        <!-- <router-view /> -->
    </div>
</template>

<script>
import ComponentCarousel from './../components/ComponentCarousel';
export default {
    components: {
        ComponentCarousel,
    },
    data() {
        return {
            tabsActive: 'first',
            gameStatus: '1',
            gameType: 0,
            gameList: [],
            orderList: this.$store.state.websocket.orderList
        }
    },
    computed: {
        homeData() {
            return this.$store.state.home.homeData;
        },
        commonData() {
            return this.$store.state.common.commonData;
        },
        orderListComputed() {
            return this.$store.state.websocket.orderList;
        }
    },
    mounted() {
        let { gameStatus, gameType } = this.$route.query;
        if (gameStatus) this.gameStatus = gameStatus;
        if (gameType) this.gameType = parseInt(gameType);
        let userInfo = this.utils.gameUserInfo();
        let { code, data } = userInfo;
        if (code == 200 && data && data.gameList) {
            // 拼接游戏类型列表
            var { gameList } =  data;
            this.gameList = [{
                id: 0,
                name: "全部",
                game_logo: require('./../assets/home/logo/homelogo.png')
            }].concat(gameList);
        }
    },
    methods: {
        handleClick(item) {
            console.log(item);
        },
        load () {
            // 异步
            this.$store.dispatch("HOME_LOAD", {
                home: 'data'
            })
            .then(response => {
                console.log(response);
            })
            // 同步
            // this.$store.commit("COMMON_DATA", {
            //     home: '我是公共数据common'
            // })
        },
        toggleGameStatus() {
            // 游戏状态切换
            // 切换状态，改变url参数
            this.utils.modifyURIParam(this, {
                gameStatus: this.gameStatus
            });
        },
        toggleGameType() {
            this.utils.modifyURIParam(this, {
                gameType: this.gameType
            });
        },
        deleteOrder(order) {
            this.$root.event.$emit("deleteOrder", order);
        }
    },
}
</script>
<style lang="less" scoped>
    @import url('./../assets/style.less');
    .page-home {
       max-width: 1246px;
        width: 1246px;
        margin: 0 auto;
        // 子导航
        .platformData {
            height: 48px;
            display: flex;
            font-size: 14px;
            color: @color-used;
            background-color: #141926;
            margin: 3px 0;
            .left {
                display: flex;
                width: 966px;
                .status, .currency {
                    display: flex;
                    align-items: center;
                    flex: 1;
                }
                .status {
                    ul {
                        display: flex;
                        list-style: none;
