                            position: relative;
                            width: 115px;
                            text-align: center;
                            height: 48px;
                            line-height: 48px;
                            cursor: pointer;
                            label {
                                display: block;
                                height: 48px;
                                line-height: 48px;
                                color: #76839F;
                                &::v-deep .el-radio__input {
                                    display: none;
                                }
                                &::v-deep .el-radio__label {
                                    padding-left: 0;
                                    span {
                                        color: #2c3e50;
                                        padding: 0 0 0 6px;
                                    }
                               }
                            }
                            // 选中的状态
                            label[aria-checked="true"]::v-deep .el-radio__label{
                                font-weight: bold;
                                color: #FFFFFF !important;
                                span {
                                    color: @color-select;
                                }
                                &::after {
                                    content: " ";
                                    position: absolute;
                                    left: 50%;
                                    bottom: 0;
                                    transform: translateX(-50%);
                                    height: 2px;
                                    width: 70px;
                                    background: @color-select;
                                }
                            }
                        }
                    }
                }
                .currency {
                    justify-content:flex-end;
                    .price {
                        width: 160px;
                        img {
                            vertical-align: middle;
                        }
                    }
                    .price:first-child {
                        img {
                            margin: -1px 4px 0 0;
                        }
                    }
                    .price:nth-child(2) {
                        img {
                            margin: -2px 4px 0 0;
                        }
                    }
                    .refresh {
                        margin: 2px 0 0 0;
                    }
                }
            }
            .right {
                display: flex;
                margin-left: 6px;
                width: 268px;
                align-items: center;
                justify-content: center;
                .record, .rule {
                    width: 80px;
                    // flex: 1;
                    text-align: center;
                    color: @color-tip;
                }
            }
        }

        .content {
            display: flex;
            & > .left{
                width: 966px;
                .game-type {
                    display: flex;
                    padding: 10px 0;
                    background-color: #1C2236;
                    margin: 0 0 3px 0;
                    .block {
                        font-size: 14px;
                        cursor: pointer;
                        box-sizing: border-box;
                        // padding: 0 15px;
                        // height: 80px;
                        margin: 0px 15px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-direction: column;
                        // border: 2px solid transparent;
                        // border-radius: 4px;
                        .image {
                            width: 30px;
                            height: 30px;
                            padding-bottom: 8px;
                            margin: 0 auto;
                            img {
                                max-width: 100%;
                            }
                        }
                        .text {
                            color: #76839F;
                            span {
                                padding: 0 0 0 6px;
                                color: #2c3e50;
                            }
                        }
                        &:hover {
                            background-color: #141926;
                        }

                        label {
                            display: block;
                            color: #76839F;
                            width: 100%;
                            height: 100%;
                            padding: 15px;
                            // margin: 0px 15px;
                            text-align: center;
                            border: 2px solid transparent;
                            border-radius: 4px;
                            box-sizing: border-box;
                            &::v-deep .el-radio__input {
                                display: none;
                            }
                            &::v-deep .el-radio__label {
                                padding-left: 0;
                                span {
                                    color: #2c3e50;
                                    padding: 0 0 0 6px;
                                }
                            }
                        }
                        label[aria-checked="true"]{
                            border-color: @color-select;
                            &::v-deep .el-radio__label{
