                            text-align: center;
                            line-height: 30px;
                            height: 30px;
                            span {
                                color: #FFFFFF;
                            }
                        }
                        .button {
                            text-align: center;
                            cursor: pointer;
                            height: 54px;
                            line-height: 54px;
                            color: #FFFFFF;
                            font-size: 18px;
                            font-weight: bold;
                            background: linear-gradient(90deg, #06DAC9, #05A7E2);
                        }
                    }
                    
                    
                }
                .myOrder {
                    .orderHead {
                        height: 50px;
                        line-height: 20px;
                        .match.color {
                            color: #FF0044;
                            background-color: #3A1821;
                        }
                        .title {
                            flex: 1;
                            padding-left: 55px;
                            .name {
                                text-align: left;
                            }
                            .num {
                                text-align: left;
                            }
                        }
                        img {
                            cursor: pointer;
                            max-width: 12px;
                        }
                    }
                    .orderContent {
                        ul {
                            position: relative;
                            li {
                                padding: 0 0 5px 0;
                                &:nth-child(2) {
                                    position: absolute;
                                    top: 10px;
                                    right: 5px;
                                    color: #28ffd4;
                                }
                                &:nth-child(4) {
                                    display: flex;
                                    padding: 20px 0 0 0;
                                    div:first-child {
                                        padding: 5px 0 0 0;
                                        flex: 1;
                                    }
                                    .price {
                                        input {
                                            background: #131825;
                                            border:1px solid #2A324F;
                                            outline: none;
                                            border: 0;
                                            width: 60px;
                                            height: 25px;
                                            line-height: 25px;
                                            text-align: center;
                                            color: #FFFFFF;
              
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &::v-deep .el-tabs__header {
                        margin: 0;
                    }
                    &::v-deep .el-tabs__nav {
