                        background: #1B2436;
                        border-bottom: 1px solid @color-line;
                        .el-tabs__item {
                            text-align: center;
                            color: #B1C4E6;
                            padding: 0;
                            width: 137px;
                        }
                        .el-tabs__active-bar {
                            background-color: @color-select;
                        }
                    }
                    &::v-deep .el-tabs__nav-wrap::after {
                        height: 0 !important;
                    }
            }
        }
    }
</style>
